.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


table {
  display: inline-block;
  border-collapse: separate;
  border-spacing: 0px;
  margin-left: 25px;
}

.mapRow {
  height: 450px;
  text-align: center;
  margin: 0 auto
}
.table {
  margin-left: 10px;
  position: relative;
}
.arrowPad {
  margin-top: 20px;
}
.arrowPad > tbody > tr > td {
  background: darkgrey;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  border-top: 2px solid lightgray;
  border-left: 2px solid lightgray;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.tableCol {
  margin: 0px;
  padding: 0px;
}
td {
  border-bottom: 4px solid grey;
  border-right: 4px solid grey;
  border-top: 4px solid rgb(234, 234, 234);
  border-left: 4px solid rgb(234, 234, 234);
}
.cell {
  width: 50px;
  height: 50px;
  font-size: 30px;
  color: white;
}
.selected {
  border: 2px dotted red;
  /*border-bottom: 4px solid black;*/
  /*border-right: 4px solid black;*/
  /*border-top: 3px red;
  border-left: 3px red;*/
}
.clicked {
  background-color: grey;
  border: 4px solid darkgrey !important;
  filter: brightness(80%);
  /*opacity: 80%;*/
}
p {
  margin-left: 31%;
}
.bomb.clicked {
  color: rgb(216, 0, 0) !important;
  font-size: 30px;
  padding: 0px;
}
.hidden {
  visibility: hidden;
}
